a {
  color: var(--link-text);
  text-decoration: none;
}

a:focus {
  box-shadow: 0 0 0 var(--link-focus);
}

a:hover {
  color: var(--link-text);
}

a:hover:not(.button-text, .image, .link-icon, .link-icon-rss, .nav-link, .navbar-brand, .photo) {
  text-decoration: underline;
}

.card {
  margin: auto;
  width: 60%;
}

.card-body {
  padding-bottom: .5rem;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
  padding-top: 1rem;
}

.link-icon {
  padding-left: .625rem;
  padding-right: .625rem;

  &:active, &:hover {
    text-decoration: none;
  }

  &:hover .icon-hover {
    display: inline;
  }

  &:hover .icon-static {
    display: none;
  }

  .icon-hover {
    display: none;
  }

  > * {
    height: 3rem;
    width: 3rem;
  }
}

.link-icon-rss {
  &:active, &:hover {
    text-decoration: none;
  }

  &:hover .icon-hover {
    display: inline;
  }

  &:hover .icon-static {
    display: none;
  }

  .icon-hover {
    display: none;
  }

  > * {
    width: 2rem;
    height: 2rem;
  }
}

@media screen and (max-width: 48rem) {
  .card {
    margin: auto;
    width: 100%;
  }

  .card-body {
    padding-bottom: .5rem;
    padding-top: 1rem;
  }
}

@media screen and (max-width: 62rem) {
  .card {
    margin: auto;
    width: 75%;
  }

  .card-body {
    padding-bottom: .5rem;
    padding-left: 1.25rem;
    padding-right: 1.25rem;
    padding-top: 1rem;
  }
}
